<template >
  <div class="bgimg" :style="bg">
    <router-view></router-view>
  </div>
</template>

<script>
export default {
  name: "App",
  created() {
    this.chageImg();
  },
  data() {
    return {
      uri: "",
      bg: {
        height: "100vh",
        backgroundImage: "",
      },
    };
  },
  methods: {
    chageImg() {
      this.$axios
        .get(
          "/proxy/HPImageArchive.aspx?format=js&idx=0&n=1&mkt=zh-CN"
        )
        .then((response) => {
          this.uri = 'https://cn.bing.com/' + response.data.images[0].url;
          this.bg.backgroundImage = "url(" + this.uri + ")";
        });

    },
  },
};
</script>

<style>
* {
  margin: 0;
  padding: 0;
}
</style>
